import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import './normalize.css';

class Container extends React.Component{
	constructor(props){
		super(props);
		this.state={
			distination:'',
			via:'',
			trainType:'',
			departureTime:'',
			platform:'',
			memo:'*   *'
		}
	}

	changeData(){

	}

	render(){
		return(
			<div>
				<div>
					<h1 className="title"></h1>
				</div>
				<div className="board-frame-tw">
					<div className="board-row first-row">
						<div className="board-subtitle"><span></span><span></span></div>
						<div id="Distination">
							<div className="english-subtitle">DISTINATION</div>
							<div className="screen sm-screen distination-screen">{this.state.distination}</div>
						</div>
						<div className="board-subtitle"><span></span><span></span></div>
						<div id="Via">
							<div className="english-subtitle">VIA</div>
							<div className="screen sm-screen via-screen">{this.state.via}</div>
						</div>
						<div className="r1-unit"></div>
						<div id="TrainType">
							<div className="english-subtitle">TRAIN TYPE</div>
							<div className="screen sm-screen traintype-screen">{this.state.trainType}</div>
						</div>
						<div className="r1-unit"></div>
					</div>
					<div className="board-row second-row">
						<div id="DepartureTime">
							<div className="english-subtitle">DEPARTURE TIME</div>
							<div className="screen departure-screen">{this.state.departureTime}</div>
						</div>
						<div className="r2-unit"></div>
						<div id="Platform">
							<div className="english-subtitle">PLATFORM</div>
							<div className="screen platform-screen">{this.state.platform}</div>
						</div>
						<div className="r2-unit"> </div>
					</div>
					<div className="board-row third-row">
						<div className="screen row-screen">
							<marquee>{this.state.memo}</marquee>
						</div>
					</div>		
				</div>

				<div class="control">
					<form>
						<fieldset>
							<legend></legend>
							<input type="text" onChange={event=>this.setState({distination:event.target.value})}/><br/>
							<input type="text" onChange={event=>this.setState({via:event.target.value})}/><br/>
							<input type="text" onChange={event=>this.setState({trainType:event.target.value})}/><br/>
							<input type="text" className="full-width-input" onChange={event=>this.setState({departureTime:event.target.value})}/>
							<span>(shift+)</span><br/>
							<input type="text" onChange={event=>this.setState({platform:event.target.value})}/>
							<span>(shift+)</span><br/>
							<div className="memo-input"><textarea type="text" onChange={event=>this.setState({memo:event.target.value})}/></div>
						</fieldset>
					</form>
				</div>
			</div>
		);
	}
};


	

	

ReactDOM.render(
  	<Container />,
  document.getElementById('root')
);


